import { useContext } from 'react';
import styled from 'styled-components';
import headerimg1 from '../../assets/headerImg1.jpg';
import HeaderCard from '../HeaderCards/HeaderCard';
import {ModalContext, ModalOnOfContext} from '../../ModalContext/ModalContext';
import Modal from '../Modal/Modal';

const HeaderBaner = ({itemsCard})=>{

  // const modal = useContext(ModalContext);
  const ModalOnOf = useContext(ModalOnOfContext);

  return (<>
    <S.Section>
      <div className='headerBanerIn'>
        <h3>Работаем по всей Беларуси</h3>
        <h1>Фундамент на винтовых сваях <br/> любой сложности <span>Всего за 1-2 дня!</span></h1>
        <h3>Без предоплаты</h3>
        <div className='cardContainer' >
          {itemsCard.map((item, index)=>{
            return <HeaderCard srcImg={item.urlImg} text={item.cardText} key={index} />
          })}
        </div>
        <h5>Хотите узнать сколько будет стоить Ваш? Закажите бесплатный расчет!</h5>
        <div className='modalContainerBtn'>
          <Modal/>
          <button className='btnHederBanner' onClick={ModalOnOf}>Бесплатный расчет</button>
        </div>
      </div>
    </S.Section>
  </>)
};

export default HeaderBaner;

const S = {
  Section: styled.section`
    width: 100%;
    background: url(${headerimg1}) 100% bottom/ cover no-repeat;
    position: relative;

    display: flex;
      flex-direction: column;
      align-items: center;
    :after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(0,0,0,.6);
      z-index: 2;
    }

    .headerBanerIn {
      position: relative;
      z-index: 3;
      color: #fff;
      padding: 50px;
      width: 70%;
      h3 {
        font-size: 2rem;
      }
      h5 {
        font-size: 1.5rem;
      }
      h1 {
        font-size: 3rem;
        span {
          display: inline-block;
          font-size: 2rem;
          background-color: red;
          transform: rotate(-5deg);
        }
      }
    }
    .cardContainer {
      display: flex;
      flex-wrap: wrap;
      align-items: flex-start;
    }
    .btnHederBanner {
      font-weight: 600;
      font-size: 1.4rem;
      background-color: red;
      color: #fff;
      width: 300px;
      height: 60px;
      border-radius: 30px;
      cursor: pointer;
      outline: none;
      :hover {
        background-color: #7A726E;
      }
    }
    .modalContainerBtn {
      position: relative;
    }
  `
}