import styled from 'styled-components';
import headerImg from '../../assets/headerImg2.jpg';
import HeaderCard from '../HeaderCards/HeaderCard';
import singlerRoof from '../../assets/ROOF/SINGLE ROOF.jpg';
import gableRoof from '../../assets/ROOF/gable roof.jpg';
import pitchedRoof from '../../assets/ROOF/pitched roof.jpg';
import reconstructionRoof from '../../assets/ROOF/reconstruction roof.jpg';
import dismantlingOfRoofs from '../../assets/ROOF/dismantling of roofs.jpg';
import ProductItem from '../Product/ProductItem';

const Roofing = ({itemsCard})=>{

  const productPresentationItem = [
    {
      itemImg: singlerRoof,
      itemText: 'Односкатная кровля', 
      imagesTxt:'singler roof',
    },
    {
      itemImg: gableRoof,
      itemText: 'Двухскатная кровля', 
      imagesTxt:'singler roof',
    },
    {
      itemImg: pitchedRoof,
      itemText: 'Многоскатная кровля', 
      imagesTxt:'singler roof',
    },
    {
      itemImg: reconstructionRoof,
      itemText: 'Реконструкция', 
      imagesTxt:'singler roof',
    },
    {
      itemImg: dismantlingOfRoofs,
      itemText: 'Демонтаж', 
      imagesTxt:'singler roof',
    },
  ];

  return (<>
  <S.Section1>
    <div className='headerBanerIn'>
        <h1>Кровельные работы по Гомелю и области</h1>
        <h2>Без предоплаты</h2>
        <h2>Под ключ</h2>
        <div className='cardContainer' >
          {itemsCard.map((item, index)=>{
            return <HeaderCard srcImg={item.urlImg} text={item.cardText} key={index} />
          })}
        </div>
        <button className='btnHederBanner'>Связаться</button>
      </div>
  </S.Section1>
  
  <S.Section2>
    <div className='productTitle'>
        <p>ПРОИЗВОДИМ МОНТАЖ РАЗЛИЧНЫХ ВИДОВ КРОВЛИ:</p>
    </div>
    <div className='productContainer'>
      {productPresentationItem.map((item, key)=>{
        return <ProductItem itemImg={item.itemImg} itemText={item.itemText} imagesTxt={item.imagesTxt} key={key}/>
      })}
    </div>
    <div className='produstBtn'>
      <button>Получить консультацию</button>
    </div>
    </S.Section2>
  </>)
};

export default Roofing;

const S = {
  Section1: styled.section`
    width: 100%;
    background: url(${headerImg}) 100% bottom/ cover no-repeat;
    position: relative;

    display: flex;
      flex-direction: column;
      align-items: center;
    :after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(0,0,0,.6);
      z-index: 2;
    }

    .headerBanerIn {
      position: relative;
      z-index: 3;
      color: #fff;
      padding: 50px;
      width: 70%;
      h3 {
        font-size: 2rem;
      }
      h5 {
        font-size: 1.5rem;
      }
      h1 {
        font-size: 3rem;
        span {
          display: inline-block;
          font-size: 2rem;
          background-color: red;
          transform: rotate(-5deg);
        }
      }
    }
    .cardContainer {
      display: flex;
      flex-wrap: wrap;
      align-items: flex-start;
    }
    .btnHederBanner {
      font-size: 1.4rem;
      background-color: red;
      color: #fff;
      width: 300px;
      height: 60px;
      border-radius: 30px;
      cursor: pointer;
      outline: none;
      font-weight: 600;
      :hover {
        background-color: #7A726E;
      }
    }
  `,
  Section2: styled.section`
    .productContainer {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }
    .productTitle {
      display: flex;
      flex-direction: column;
      align-items: center;
      font-size: 1.8rem;
      p {
        font-size: 1.5rem;
        font-weight: 600;
      }
    }
    .produstBtn {
      display: flex;
      justify-content: center;
      margin: 20px 0;
      button {
        font-size: 1.3rem;
        font-weight: 600;
        background-color: red;
        color: #fff;
        outline: none;
        display: inline-block;
        cursor: pointer;
        height: 50px;
        padding: 0 20px;
        border-radius: 20px;
        :hover {
          background-color: #7A726E;
        }
      }
    }
  `
}