import styled from 'styled-components';
import bath from '../../assets/forImg/bath.jpg';
import home from '../../assets/forImg/home.jpg';
import garage from '../../assets/forImg/garage.webp';
import pier from '../../assets/forImg/pier.jpg';
import greenhouse from '../../assets/forImg/greenhouse.jpg';
import gazebos from '../../assets/forImg/gazebos.jpg';
import canopy from '../../assets/forImg/canopy.jpg';
import terraces from '../../assets/forImg/terraces.jpg';
import annexes from '../../assets/forImg/annexes.jpg';
import verandas from '../../assets/forImg/verandas.jpg';
import fence from '../../assets/forImg/fence.jpg';
import bridge from '../../assets/forImg/bridge.jpg';
import ProductItem from './ProductItem';
import Modal from '../Modal/Modal';
import {ModalContext, ModalOnOfContext} from '../../ModalContext/ModalContext';
import { useContext } from 'react';

const Product = ()=>{

  const productPresentationItem = [
    {
      itemImg: home,
      itemText: 'Дома', 
      imagesTxt:'home',
    },
    {
      itemImg: bath,
      itemText: 'Бани', 
      imagesTxt:'bath',
    },
    {
      itemImg: garage,
      itemText: 'Гаража', 
      imagesTxt:'garage',
    },
    {
      itemImg: pier,
      itemText: 'Пирса', 
      imagesTxt:'pier',
    },
    {
      itemImg: greenhouse,
      itemText: 'Теплицы', 
      imagesTxt:'greenhouse',
    },
    {
      itemImg: gazebos,
      itemText: 'Беседки', 
      imagesTxt:'gazebos',
    },
    {
      itemImg: canopy,
      itemText: 'Навеса', 
      imagesTxt:'canopy',
    },
    {
      itemImg: terraces,
      itemText: 'Терассы', 
      imagesTxt:'terraces',
    },
    {
      itemImg: annexes,
      itemText: 'Пристройки', 
      imagesTxt:'annexes',
    },
    {
      itemImg: verandas,
      itemText: 'Веранды', 
      imagesTxt:'verandas',
    },
    {
      itemImg: fence,
      itemText: 'Забора', 
      imagesTxt:'fence',
    },
    {
      itemImg: bridge,
      itemText: 'Мостика', 
      imagesTxt:'bridge',
    },
  ];
  const ModalOnOf = useContext(ModalOnOfContext);

  return (<>
    <S.Section className='product'>
      <div className='productTitle'>
        <h1>СВАЙНО-ВИНТОВОЙ ФУНДАМЕНТ</h1>
        <p>ЛУЧШЕЕ РЕШЕНИЕ ДЛЯ:</p>
      </div>
      <div className='productContainer'>
        {productPresentationItem.map((item, key)=>{
          return <ProductItem itemImg={item.itemImg} itemText={item.itemText} imagesTxt={item.imagesTxt} key={key}/>
        })}
      </div>
      <div>
        {/* <Modal/> */}
        <div className='produstBtn'>
          <button onClick={ModalOnOf}>Получить консультацию</button>
        </div>
      </div>
      
    </S.Section>
  </>)
};

export default Product;

const S = {
  Section: styled.section`
    .productContainer {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }
    .productTitle {
      display: flex;
      flex-direction: column;
      align-items: center;
      font-size: 1.8rem;
      p {
        font-size: 1.5rem;
        font-weight: 600;
      }
    }
    .produstBtn {
      display: flex;
      justify-content: center;
      margin: 20px 0;
      button {
        font-size: 1.3rem;
        font-weight: 600;
        background-color: red;
        color: #fff;
        outline: none;
        display: inline-block;
        cursor: pointer;
        height: 50px;
        padding: 0 20px;
        border-radius: 20px;
        :hover {
          background-color: #7A726E;
        }
      }
    }
  `
};