import styled from 'styled-components';
import { useContext } from "react";
import {ModalContext, ModalOnOfContext} from '../../ModalContext/ModalContext';

const Modal = ()=>{

  const modal = useContext(ModalContext);
  const modalOnOf = useContext(ModalOnOfContext);

  // const click
  if(!modal) return null

  return (<>
    <S.Wrapper>
      <div><p>Имя</p><input type='text'/></div>
      <div><p>Телефон</p><input type='tel'/></div>
    </S.Wrapper>
  </>)
};

export default Modal;

const S = {
  Wrapper: styled.div`
    position: absolute;
    right: 50%;
    background-color: #fff;
    width: 50vh;
    height: 50vh;
    div {
      display: flex;

      p {
        font-size: 1rem;
        color: #000;
        font-weight: bold;
      }
    }
  `
}