import styled from 'styled-components';
import viber from '../../assets/VIBER-LOGO.png';
import velcom from '../../assets/logoVelcom.jpg';
import MainPage from '../MainPage/MainPage';

const HeaderNav =()=>{

  return(<>
    <S.Header className="header">
        <nav className="header-nav">
          <MainPage/>
          <div className='headerNavKontakt'>
            <h3>Контакты</h3>
            <div className='headerTel'>
              <img src={velcom} alt='velcom logo' className='header-velcom-logo'/>
              <a href='tel: +375296640846' className='tel'>(29) 664 08 46</a>
              <img src={viber} alt='viber logo' className='header-viber-logo'/>
            </div>
            <div className='headerTel'>
              <img src={velcom} alt='velcom logo' className='header-velcom-logo'/>
              <a href='tel: +375293288732' className='tel'>(29) 328 87 32</a>
              <img src={viber} alt='viber logo' className='header-viber-logo'/>
            </div>
          </div>
        </nav>
    </S.Header>
  </>)
};

export default HeaderNav;

const S = {
  Header: styled.header`
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: sticky;
    z-index: 10;
    top: 0;
    background-color: #fff;
    height: 90px;

    .header-velcom-logo, .header-viber-logo {
      width: 18px;
      height: 18px;
      margin: 0 5px;
    }

    .header-nav {
      display: flex;
      width: 100%;
      justify-content: space-around;
      .headerNavKontakt {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .headerTel {
          border: 2px solid black;
          display: flex;
          margin: 5px 0;
          padding: 2px 0;
          background-color: #5C5C5C;
          .tel {
            text-decoration: none;
            color: #000;
            font-weight: 800;
            color: #fff;
          }
        }
        .headerTel:hover {
          background-color: #fff;
          .tel{
            color: #000;
          }
        }
        h3 {
          margin: 0;
          padding: 0;
        }
      }
    }
  `
};